<template>
  <div id="app"
    :class="type">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    data() {
      return {
        type: 'mobile'
      }
    },
    created() {
      if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
        this.type = 'mobile'
      } else {
        this.type = 'PC'
      }
    },
  }
</script>

<style>
  .PC{
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 0 auto;
    width: 375px;
    position: absolute;
    left: 50%;
    margin-left: -187.5px;
    /* border: 1px solid #000; */
    height: 667px;
    top: 50%;
    margin-top: -333.5px;
  }
  .mobile{
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
  }
</style>



