<template>
  <div class="hello">
    <div id="main">
      <!-- <iframe style="display: none;" src="http://www.5nd.com/fx/fx.html?id=642507" frameborder="0" allowfullscreen="true" width="0" height="0"></iframe> -->
      <img src="./hert.png" v-if="heartflag" class="heartDong" @click="startAnimitate">
      <canvas v-if="!imgtree" id="canvas" width="375" height="667"></canvas>
      <img :src="imgtree" v-else class="imgClass">
      <canvas v-if="imgtree" id="RedHeart" width="375" height="667"></canvas>
      <div id="text" v-show="imgtree">
        <iframe id="Pendulum" style="display: none;width: 100%;margin-bottom: 15px;" src="//player.bilibili.com/player.html?aid=13579774&cid=22226480&page=1" scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true"> </iframe>
        <div id="code">
          <span class="say">我想牵着你的手，一生一世，一双人。</span><br>
          <span class="say">我想陪你去爬庐山，陪你去看大海。</span><br>
          <span class="say">我想陪你种水果，养狗，养猫，看星星，</span><br>
          <span class="say">每一天带着开心，我都会和你说早安，</span><br>
          <span class="say">每一晚带着微笑，我都会和你说晚安，</span><br>
          <span class="say">我会带你去所有你想去的地方，</span><br>
          <span class="say">陪你做任何你想做的事情，</span><br>
          <span class="say">共同历经你生命中的点点滴滴。</span><br>
          <span class="say">我希望这一生与你一起走过，</span><br>
          <span class="say">就像这一部 <span style="color: blue;cursor: pointer;" onclick="openvideo()">《钟摆》</span>动画短篇里一样。</span><br>
          <br>
          <span class="say">我会为我们的未来撑起一片天空，</span><br>
          <span class="say">为我们的将来担负起一生的责任，</span><br>
          <span class="say">愿意为你去做每一件能让你开心快乐的事。</span><br>
          <span class="say">对于我来说最幸福的事情，</span><br>
          <span class="say">就是和你一起慢慢变老！</span><br>
          <br>
          <span class="say"><span class="space"></span> -- 你的, 解飞。</span>
        </div>
      </div>
      <div id="clock-box">
        <a href="javascript:void(0);">解飞</a> 与 <a href="javascript:void(0);">王娆</a> 相遇的
        <div id="clock"></div>
      </div>
    </div>
  </div>
</template>

<script>
  window.openvideo = function() {
    var dom = document.getElementById('Pendulum')
    dom.style.display = 'block'
  }
  let vm = {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        imgtree: '',
        heartflag: true,
        videoFlag: false
      }
    },
    created() {
      this.$nextTick(() => {
        // let video = document.getElementById('myaudio')
        // video.src = './static/love.mp3';
        // video.load();
        this.__init()
      })
    },
    methods: {
      play() {
        let video = document.getElementById('myaudio')
        video.play();
      },
      __init() {
        console.log('初始化')
        window.animateTree(img => {
          this.imgtree = img
        }, () => {
          this.play()
          this.heartflag = false
        })
      },
      startAnimitate() {
        // alert('sdsd');
        console.log('sdsdsd')
      }
    },
  }
  export default vm
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #canvas {
    position: absolute;
    /* left: -300px; */
    /* bottom: 0; */
  }
  .imgClass {
    animation: scaleTree ease-in-out 5s;
    transform: scale(.5);
    transform-origin: 100% 100% 0;
    position: absolute;
    z-index: 1;
  }
  @keyframes scaleTree {
    0% {
      transform: scale(1);
      transform-origin: 100% 100% 0;
    }
    100% {
      transform: scale(.5);
      transform-origin: 100% 100% 0;
    }
  }
  #clock-box {
    position: absolute;
    left: 15px;
    bottom: 30px;
    font-size: 10px;
    display: none;
  }
  #clock-box a {
    font-size: 16px;
    text-decoration: none;
  }
  #clock {
    margin-left: 30px;
  }
  #clock .digit {
    font-size: 25px;
  }
  #text {
    /* width: px; */
    height: 425px;
    left: 15px;
    top: 15px;
    right: 15px;
    position: absolute;
    z-index: 3;
  }
  #code {
    display: none;
    font-size: 16px;
  }
  #RedHeart {
    background: pink;
    position: absolute;
    left: 0;
    top: 0;
  }
  .heartDong {
    position: absolute;
    width: 36px;
    height: 36px;
    left: 50%;
    margin-left: -18px;
    top: 50%;
    margin-top: -18px;
    animation-name: scaleDraw;
    /*关键帧名称*/
    animation-timing-function: ease-in-out;
    /*动画的速度曲线*/
    animation-iteration-count: infinite;
    /*动画播放的次数*/
    animation-duration: 5s;
    /*动画所花费的时间*/
  }
  @keyframes scaleDraw {
    /*定义关键帧、scaleDrew是需要绑定到选择器的关键帧名称*/
    0% {
      transform: scale(1);
      /*开始为原始大小*/
    }
    25% {
      transform: scale(1.5);
      /*放大1.1倍*/
    }
    50% {
      transform: scale(1);
    }
    75% {
      transform: scale(1.5);
    }
  }
</style>



